import oakHeader from "../components/header/header";
import oakFooter from "../components/footer/footer";
export default {
  name: "contact",
  components: {
    oakHeader,
    oakFooter,
  },
  data() {
    return {
      name: "",
      name_error: "",
      company_name: "",
      company_name_error: "",
      phone: "",
      phone_error: "",
      code: "",
      code_error: "",
      pageShow: false,
      sec_list: [
        {title: '公司介绍', url: "/about", type: 'about'},
        {title: '联系我们', url: "/contact", type: 'contact'}
      ]
    };
  },
  head() {
    return {
      title: "橡树黑卡--联系我们",
      meta: [
        {
          hid: "about",
          name: "description",
          content:
            "完善您的信息后，橡树黑卡会有专门的商务人员与您对接，400-150-9669。",
        },
        {
          name: "keywords",
          content:
            "付费会员，会员权益，高新技术企业，提升活跃，带动营收，增加收入，客户关怀，用户画像，延长用户生命周期",
        },
      ],
    };
  },
  mounted() {
    this.pageShow = true;
  },
  methods: {
    getCode() {
      //获取验证码
    },
    getPlan() {
      this.showDialog = true;
      if (!this.name) {
        this.name_error = "请填写您的姓名";
        return;
      } else {
        this.name_error = "";
      }
      if (!this.phone) {
        this.phone_error = "请填写您的联系电话";
        return;
      } else {
        if (!/^1([3-9])\d{9}$/.test(this.phone)) {
          this.phone_error = "请填写正确的联系电话";
          return;
        }
        this.phone_error = "";
      }
      if (!this.code) {
        this.code_error = "请填写验证码";
        return;
      } else {
        this.code_error = "";
      }
    },
  },
};
